@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Merriweather:wght@300;400;700&family=Titillium+Web:wght@300;400;600;700;900);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Merriweather:wght@300;400;700&family=Titillium+Web:wght@300;400;600;700;900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@media only screen and (max-width: 600px) {
  html,
  body {
    overflow-y: hidden;
    max-height: 100vh;
    max-width: 100vw;
    overflow-x: scroll;
  }
}

@media only screen and (max-width: 600px) {
  .App {
    display: flex;
  }
}

.bg {
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  position: relative;
  color: #101011;
}

.fixed {
  background-attachment: fixed;
}

.bg1 {
  background-image: url(/Portfolio/static/media/AboutMe.ffb9467a.png);
}
.bg2 {
  background-image: url(/Portfolio/static/media/SF-project.dcfd033e.png);
}
.bg3 {
  background-image: url(/Portfolio/static/media/RN-project.ebffa5b4.png);
}
.bg4 {
  background-image: url(/Portfolio/static/media/FaceApp.541e976e.png);
}
.bg5 {
  background-image: url(/Portfolio/static/media/RoboFriends.fcb35d36.png);
}
.bg6 {
  background-image: url(/Portfolio/static/media/postIt.e77952f7.png);
}
.bg7 {
  background-image: url(/Portfolio/static/media/fourdoortour.23c55694.png);
}

.info {
  height: 100%;
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1vw;
}

.title {
  font-size: 3.9em;
  font-family: "Bebas Neue", cursive;
  font-weight: 700;
  text-align: center;
  width: 60%;
}

.divider {
  width: 30%;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.2);
}

.text-container {
  top: 1em;
  width: 70%;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 10px;
}

.background-text {
  margin-left: -50px;
  position: absolute;
  font-size: 7vw;
  line-height: 150px;
  opacity: 0.1;
  font-weight: 900;
  text-align: center;
  text-transform: uppercase;
  font-family: "Titillium Web", sans-serif;
}

.text {
  text-align: left;
  font-size: 1.7em;
  font-family: "Merriweather", serif;
  line-height: 45px;
}

@media only screen and (max-width: 1200px) {
  .fixed {
    background-attachment: unset;
  }
  .text-container {
    top: 0;
    width: 100%;
    font-size: 12px;
    padding: 0;
  }
  .info {
    height: 50%;
    width: 80%;
  }
  .title {
    font-size: 5vh;
    width: 90%;
    margin-bottom: 10px;
  }
  .divider {
    margin-bottom: 20px;
  }
  .text {
    font-size: 2vw;
    line-height: 30px;
  }
  .background-text {
    font-size: 15vw;
    margin: 0;
    line-height: 10vh;
    opacity: 0.05;
  }
  .bg1 {
    background-image: url(/Portfolio/static/media/AboutMeTablet.07ad5b5a.png);
  }
  .bg2 {
    background-image: url(/Portfolio/static/media/SF-projectTablet.b4e9232e.png);
  }
  .bg3 {
    background-image: url(/Portfolio/static/media/RN-tablet.ff42e092.png);
  }
  .bg4 {
    background-image: url(/Portfolio/static/media/FaceAppTablet.0a3b10e8.png);
  }
  .bg5 {
    background-image: url(/Portfolio/static/media/RoboFriendsTablet.bc37a52e.png);
  }
  .bg6 {
    background-image: url(/Portfolio/static/media/postitTablet.05d5c0ec.png);
  }
  .bg7 {
    background-image: url(/Portfolio/static/media/fourdoortourTablet.2b7dd67b.png);
  }
  .bg {
    background-position: top;
    background-size: contain;
    position: relative;
    min-height: 650px;
    width: 100vw;
    background-attachment: local;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }
}

@media only screen and (max-width: 600px) {
  .fixed {
    background-attachment: fixed;
  }
  .bg {
    min-width: 100vw;
    background-attachment: fixed;
  }
  .info {
    height: 65%;
    width: 90%;
  }
  .title {
    line-height: 40px;
    width: 80%;
  }
  .text {
    font-size: 3vw;
    width: 80%;
    line-height: 20px;
  }
  .background-text {
    font-size: 11vw;
    line-height: 50px;
  }
}

header,
footer {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: #2e3033;
}
h1 {
  font-weight: 900;
  text-transform: uppercase;
  font-family: "Titillium Web", sans-serif;
  font-size: 10em;
  line-height: 150px;
  margin: 0;
}
p {
  margin: 0;
}
.container {
  width: 81%;
  height: 100%;
  color: #9c968a;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.menu {
  display: flex;
  justify-content: flex-end;
}
.initials {
  position: absolute;
  left: 0px;
  font-family: "Titillium Web", sans-serif;
  font-size: 4em;
  color: #9c968a;
  font-weight: 900;
}
.menu ul {
  display: flex;
  z-index: 15;
}
li {
  list-style: none;
}
li a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: 2px solid;
  border-radius: 50%;
  font: inherit;
  line-height: 1;
  margin: 0.5em;
  padding: 1em;
  color: #69ff96;
  transition: 0.25s;
}
button:hover {
  border: 2px solid #5cf4ff;
  color: #fff;
}
button:focus {
  outline: 0;
}

.main-button {
  border-radius: 10px;
  padding: 1em 2em;
}
.raise {
  color: #5cf4ff;
}
.raise:hover {
  box-shadow: 0 0.5em 0.5em -0.4em #5cf4ff;
  transform: translateY(-0.25em);
  color: #5cf4ff;
  cursor: pointer;
}
.scrolled {
  /*transition: color 100ms linear;*/
  color: #2e3033;
}
.scrolled:hover {
  box-shadow: 0 0.5em 0.5em -0.4em #2e3033;
  transform: translateY(-0.25em);
  color: #2e3033;
}
.main {
  position: relative;
  bottom: 10%;
}
.mb-icon {
  font-size: 1.3em;
  margin-bottom: 10%;
}
.welcome {
  font-size: 2em;
}
#name {
  font-weight: 600;
  color: #5cf4ff;
}
.contact {
  justify-content: flex-end;
}
.last-page {
  margin-bottom: 10%;
}
.thanks {
  font-size: 2em;
}
.contact-info {
  position: absolute;
  bottom: 0;
  padding-bottom: 1%;
  text-align: center;
  width: 100%;
  color: #9c968a;
}
@media only screen and (max-width: 1200px) {
  h1 {
    font-size: 6em;
  }
  .title-name {
    line-height: 9vh;
  }
  .contact {
    justify-content: center;
    align-items: center;
    width: 70%;
    text-align: center;
  }
  .thanks {
    font-size: 5vw;
  }
  .footnote {
    margin: 50px 0 100px 0;
    font-size: 5em;
    line-height: 70px;
  }
  .main-button {
    padding: 1em 1.5em;
  }
}
@media only screen and (max-width: 600px) {
  header,
  footer {
    min-width: 100vw;
  }
  .title-name {
    line-height: 7vh;
  }

  .initials {
    font-size: 3em;
  }
  .welcome {
    font-size: 1.5em;
  }
  h1 {
    font-size: 3.7em;
    line-height: 50px;
  }
  .contact-info {
    font-size: 3vw;
  }
}

